.sfas-carousel .swiper-slide h4 {
  margin-bottom: 0.75rem;
  text-align: left;
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.5rem;
  --tw-text-opacity: 1;
  color: rgb(0 0 0 / var(--tw-text-opacity));
}

